div.column_wrapper div.content_wrapper {
  width: 100vw;
  display: flex;
  align-items: flex-start;
  align-content: flex-start;

  padding-top: 30px;
  padding-bottom: 30px;
}
div.column_wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  align-content: flex-start;
}

section.inner_content div.column_content.flex {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  padding-top: 20px;
  padding-bottom: 20px;
}
section.inner_content div.column_content.flex.scroller {
  /* width: 100%; */
  /* max-width: 1300px; */
  /* min-height: 355px; */
  /* overflow-x: scroll; */
  overflow: auto;
  transition: height 0.5s linear;
  overflow-y: hidden;
}

div.column_wrapper div.column {
  width: 100%;
}

section.inner_content
  div.media.discover
  div.column_content
  div.card.style_1
  div.image {
  border-radius: 0.5rem;
  width: 100%;
  min-height: calc(150px * 1.5);
  height: calc(150px * 1.5);
  background: #dbdbdb;
  overflow: hidden;
}

.style_1 {
  width: 150px;
  min-width: 150px;
  border: none;
  box-shadow: none;
}

div.media.discover div.card.style_1 {
  margin-left: 30px;
  position: relative;
}

div.media.discover div.card.style_1 div.content a {
  font-weight: 700;
  color: #000;
}

.span-title {
  font-size: 12px;
  border: bold;
  font-weight: bold;
}

